/**
 *  id 
 * @param {HTMLElement} containter  html 
 */
export default containter => {
  let anchors = [];

  if (!containter) {
    return anchors;
  }

  const nodeList = containter.querySelectorAll("[id]");

  nodeList.forEach(node => {
    const id = node.getAttribute("id");

    if (id.includes("example-api-")) {
      return;
    }

    anchors.push(id);
  })

  return anchors;
};


